@import url(https://fonts.googleapis.com/css?family=Audiowide&display=swap);
body{margin:0;font-family:"Roboto Mono", monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body svg{width:20px;margin-right:4px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.dark{color:#4bb9a7;background-color:#0d1117}.dark #projects .project-item{border-bottom:1px dashed #4e4e4e}.dark #projects .project-item:hover{border:1px solid #303030}.dark section{border-bottom:1px solid #2c2c2c}.dark h4{color:#dad8d8;font-weight:bolder}.dark p{color:#dad8d8}.dark #google{background-color:#161b22}.dark #google input{background-color:#1b1b1a;color:#ffffff}.dark #google li a{color:white}.dark .buttons .button{color:#cfcec5}.light{background-color:#fff}.light section{border-bottom:1px solid #eeeeee}.light #projects .project-item:hover{border:1px solid #eee}.light #google{background-color:white}.light #google input{background-color:white;color:black}.light #google li a{color:#000000}.light #google .buttons .button{background-color:#f8f9fa;color:black}.light #google .buttons .button:hover{border:1px solid #c8c8c8}.light #google .buttons .button:focus{border:1px solid #4885ed}

.App{padding:10px;min-height:100vh;width:100%}.App h4{font-weight:bolder}.general{color:white}section{margin-top:60px;max-width:900px}ul{margin:20px;padding:0}

#google{max-width:700px;max-height:500px;margin:10px auto;border-radius:20px;border:1px solid #caffff;padding:0 5px;margin-top:80px}#google ul{list-style-type:none;overflow:hidden}#google li{float:right}#google li a{color:#ffffff;display:block;text-align:center;padding:10px 10px;text-decoration:none;font-size:14px}#google li a:hover{text-decoration:underline}#google .grid{height:23px;position:relative;bottom:4px}#google .signbutton{background-color:#4885ed;color:#fff;border:none;border-radius:3px;padding:7px 10px;position:relative;bottom:7px;font-weight:bold}#google .google-logo{display:flex;justify-content:center;margin:100px 0 20px 0}#google .searchbar{margin:0 auto;width:100%;border-radius:30px;border:1px solid #dcdcdc;display:flex;align-items:center}#google .searchbar input{height:45px;border:none;font-size:16px;outline:none;border-radius:30px 0 0 30px;flex-grow:1;padding-left:20px;letter-spacing:1px}#google .searchbar img{padding:0 10px 0 0;border-radius:0 30px 30px 0;cursor:pointer;width:30px;height:30px}#google .searchbar:hover{box-shadow:1px 1px 2px 1px #dcdcdcc4}#google .searchbar:focus-within{box-shadow:1px 1px 2px 1px #dcdcdcc4;outline:none}#google .buttons{margin-top:30px;display:flex;justify-content:center}#google .buttons .button{background-color:#1e1f1e;border:none;font-size:15px;padding:10px 20px;margin:5px;border-radius:4px;outline:none}#google .buttons .button:hover{border:1px solid #c8c8c8;padding:9px 19px;color:#808080}#google .buttons .button:focus{border:1px solid #4885ed;padding:9px 19px}@media (max-width: 600px){#google .google-logo img{width:150px}#google .searchbar{height:45px}#google .searchbar input{height:100%;padding-left:5px}#google .searchbar img{width:25px;height:20px}#google .buttons .button{font-size:12px;padding:10px 20px;margin:5px;border-radius:4px;outline:none}#google .google-logo{margin:50px 0 20px 0}}

.loader{color:#4bb9a7;font-size:60px;text-indent:-9999em;overflow:hidden;width:1em;height:1em;border-radius:50%;margin:72px auto;position:relative;transform:translateZ(0);animation:load6 1.7s infinite ease, round 1.7s infinite ease}@keyframes load6{0%{box-shadow:0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em}5%,95%{box-shadow:0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em}10%,59%{box-shadow:0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em}20%{box-shadow:0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em}38%{box-shadow:0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em}100%{box-shadow:0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em}}@keyframes round{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

*{box-sizing:border-box}body{margin:0px;font-family:"segoe ui"}.nav{height:40px;background-color:#282c34;position:fixed;z-index:10;left:0;top:0;right:0;display:flex;justify-content:space-around}.nav .nav-header .nav-title{display:inline-block;font-size:22px;color:#fff;padding:10px}.nav .nav-links button{background-color:inherit;border:none;color:white;padding:10px;display:inline;border-radius:30px;font-size:16px;cursor:pointer}.nav .nav-links button:hover{color:#00f75f}@media (max-width: 768px){.nav .nav-links button{font-size:13px;padding:10px 8px}}

#theme-toggle{display:inline}#theme-toggle .nav-title{display:flex;align-items:center}#theme-toggle .switch{position:relative;display:inline-block;width:40px;height:20px}#theme-toggle .switch input{opacity:0;width:0;height:0}#theme-toggle .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;transition:0.4s}#theme-toggle .slider:before{position:absolute;content:"";height:15px;width:15px;left:4px;bottom:3px;background-color:white;transition:0.2s}#theme-toggle input:checked+.slider{background-color:#3c2d86}#theme-toggle input:focus+.slider{box-shadow:0 0 1px #4bb9a7}#theme-toggle input:checked+.slider:before{transform:translateX(18px)}#theme-toggle .slider.round{border-radius:34px}#theme-toggle .slider.round:before{border-radius:50%}

section.education ul li{margin:15px 0}section.education a{color:white}

footer{margin:0 0 100px 0;width:100%;text-align:center}footer .copyright{color:#a3a3a3;text-align:start;margin:10px 0;font-size:11px}footer img{margin:0 5px;background:#4bb9a7;border-radius:12px}

