.resume_wrapper{display:flex;width:793px;margin:0px auto 0px auto;background:#fff;min-height:1122.519685px;border:1px solid #84838b}.resume_wrapper section{margin:10px 10px 0px 10px}.resume_wrapper .profile{color:black}.resume_wrapper .heading-secondary{color:#5ba7d1}.resume_wrapper .contact-me-info{display:flex;line-height:25px;white-space:nowrap}.resume_wrapper .skills-summary{margin:30px}.resume_wrapper .skills-summary ul{margin-left:40px;line-height:27px;white-space:nowrap}.resume_wrapper .resume_namerole .name{font-size:20px;color:#fff;text-transform:uppercase;font-weight:700;margin-bottom:5px;letter-spacing:4px;line-height:30px}.resume_wrapper .resume_left{background:#335384;color:#e2e2e2}.resume_wrapper .resume_left .resume_image{width:100%}.resume_wrapper .resume_left .resume_image img{display:flex;margin:30px auto 0 auto}.resume_wrapper .title{color:#fff;text-transform:uppercase;font-weight:700;font-size:16px;margin-bottom:10px;letter-spacing:4px}.resume_wrapper .resume_item{padding:20px 0;border-bottom:0px solid #335384}.resume_wrapper .resume_left .resume_namerole{display:none}.resume_wrapper .resume_left .resume_namerole .role{color:#e9e9e9}.resume_wrapper .resume_right{width:65%;padding:20px 10px;color:#335384}.resume_wrapper .resume_right .resume_namerole .name{color:#335384;font-size:32px}.resume_wrapper .resume_right .resume_namerole .role{font-size:14px;text-transform:uppercase}.resume_wrapper .resume_right .resume_title{color:#335384}.resume_wrapper .resume_right .resume_data{display:flex}.resume_wrapper .resume_right .resume_data .year{width:115px;position:relative}.resume_wrapper .resume_right .resume_data .content{padding-left:35px;margin-bottom:20px;width:calc(100% - 115px)}.resume_wrapper .resume_right .resume_data .year:before{content:"";position:absolute;top:5px;right:0;width:10px;height:10px;background:#fff;border:1px solid #335384;border-radius:50%}.resume_wrapper .resume_right .resume_data .year:after{content:"";position:absolute;top:17px;right:5px;width:2px;height:90%;background:#335384}.resume_wrapper .resume_right .resume_data:last-child .year:after{display:none}

